<template>
  <crudility-modal
    v-model="dialog"
    max-width="600px"
  >
    <template v-slot:activator="{ on }">
      <crudility-button v-on="on">{{ $t('delete')}}</crudility-button>
    </template>
    <v-card>
      <v-card-title class="headline">
        {{ $t('title') }}
      </v-card-title>
      <v-card-text>
        {{ $t('text') }}
      </v-card-text>
      <v-card-actions>
        <v-spacer />
        <v-btn
          color="grey darken-1"
          text
          @click="dialog = false"
        >
          {{ $t('no') }}
        </v-btn>
        <v-btn
          color="red darken-1"
          text
          @click="$emit('confirm')"
        >
          {{ $t('yes') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </crudility-modal>
</template>

<script>
import CrudilityModal from '../ui/CrudilityModal'
import CrudilityButton from '../ui/button/CrudilityButton'
export default {
  name: 'ConfirmationDialog',
  components: {
    CrudilityButton,
    CrudilityModal
  },
  model: {
    prop: 'dialog',
    event: 'change'
  },
  props: {
    dialog: Boolean
  }
}
</script>

