<template>
  <v-container class="crudility-search-module" fluid>
    <div class="crudility-search-module__filter">
      <crudility-jql-filter
        id="search"
        ref="filter"
        :d-options="dOptions"
        :loading="loading"
        :line="query ? query.line : ''"
        @option-click="onOptionClick"
        @input-field-change="onInputSearchField"
        @search="onSearch"
      >
        <template #prepend>
          <crudility-status
            :status="query.validation.status"
            :message="query.validation.message"
          />
        </template>

        <template #append>
          <crudility-hint/>
        </template>

        <template #append-outer>
          <crudility-button @click="onSearch(query.input)">
            {{ $t('search') }}
          </crudility-button>
        </template>
      </crudility-jql-filter>
    </div>

    <div
      class="crudility-search-module__create-btn"
      v-if="query.rootEntity"
    >
      <crudility-button
        @click="onCreate"
      >
        {{ $t('add') }}
      </crudility-button>
    </div>

    <crudility-table
      class="crudility-search-module__table"
      :table="table"
      @reload-table="changeTableOptions"
      @row-click="onTableRowClick"
      @compute-total-count="getTableTotalCount"
      @search="onSearch"
    />
  </v-container>
</template>

<script>
import debounce from 'lodash/debounce'
import cloneDeep from 'lodash/cloneDeep'
import concat from 'lodash/concat'
import filter from 'lodash/filter'
import map from 'lodash/map'

import CrudilityJqlFilter from './common/CrudilityJqlFilter.vue';
import CrudilityStatus from './common/CrudilityStatus.vue';
import CrudilityTable from './ui/CrudilityTable.vue';
import CrudilityButton from './ui/button/CrudilityButton.vue';
import TableController from '../assets/js/filter/TableController'
import QueryController from '../assets/js/filter/QueryController'
import OptionsController from '../assets/js/filter/OptionsController'
import EntitySelectionState from '../assets/js/states/EntitySelectionState'
import { Parser } from '../assets/js/filter/Parser'
import Line from '../assets/js/filter/Line'
import api from '../api'
import CrudilityHint from './custom/CrudilityHint'

export default {
  name: 'CrudilitySearchModule',
  components: {
    CrudilityHint,
    CrudilityButton,
    CrudilityTable,
    CrudilityStatus,
    CrudilityJqlFilter,
  },
  props: {
    queryParams: {
      type: String,
      default: ''
    },
    schema: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      dOptions: new OptionsController(),
      loading: false,
      state: new EntitySelectionState(this),
      query: new QueryController(this),
      table: new TableController(),
      tableData: []
    }
  },
  watch: {
    queryParams () {
      if (!this.queryParams) {
        this.resetFilter()
      }
    }
  },
  mounted () {
    this.debouncedClick = debounce(this.onOptionClick, 50)
    this.queryParams ? this.parseQueryParams(this.queryParams) : this.resetFilter()
  },
  methods: {
    onSearch (query) {
      if (query){
        this.setQueryToUrl(query)
        this.parseQueryParams(query)
      }
    },

    onCreate () {
      const queryData = { entity: this.query.rootEntity.name }
      this.$emit('switch-page', 'create', queryData)
    },

    parseQueryParams (query) {
      this.query.setState(query)
      this.state.forwardFilter()
      if (!this.state.isTableUpdated) {
        this.updateTable(new Line(this.query.line).techLine())
      }
    },

    onInputSearchField (input) {
      this.setQueryToUrl(input)
      this.query.input = input

      const trimmedInput = input ? input.trim() : input
      if (!trimmedInput) {
        this.dOptions.isVisible = false
        this.resetFilter()
        return
      }
      if (this.state.isLastPartDeleted(Parser.cdLast(input), Parser.cdLast(this.query.line))) {
        this.query.deletePart()
      } else if (this.query.isNeedsToDowngrade(input)) {
        this.downgradeFilter(input)
        return
      }

      const lastChars = this.query.getChanges(input).trim()
      this.state.onInputHandler(input, lastChars)
    },

    downgradeFilter (input) {
      this.query.downgraded = true
      this.state = this.state.prev()
      const lastChars = this.query.getStringDiff(input, new Line(this.query.line).truncatedQueryLine()).trim()
      this.state.downgradeFilter(lastChars)
    },

    /**
     * @param lastChars
     */
    filterOptions (lastChars) {
      this.dOptions.filter(lastChars)
    },

    /**
     *        
     */
    resetFilter () {
      this.query.reset()
      this.state = new EntitySelectionState(this)
      this.state.forwardFilter('')
    },

    /**
     *     
     * @param {string} option    
     */
    async onOptionClick (option) {
      await this.state.appendOrReplacePart(option)
      if (this.state.link) {
        this.state.updateLinkOptions()
      } else {
        this.state = this.state.next()
        this.state.forwardFilter(option)
      }
      this.setQueryToUrl(this.query.line)
    },

    async updateTable (newQuery, resetTableOptions = true) {
      if (resetTableOptions) {
        this.table.resetPageAndOffset()
      }
      const response = await this.asyncRequest('fetchData', {
        query: newQuery,
        limit: this.table.itemsPerPage,
        offset: this.table.offset
      })
      const { data } = response
      this.table.serverItemsLength = data.hasFurtherData ? this.table.getApproximateCount() : Number(data.totalCount)
      this.table.hasFurtherData = data.hasFurtherData
      this.applyData(data.entities)
    },

    applyData (data) {
      if (data) {
        this.tableData = cloneDeep(data)
        this.table.updateTable(data, this.query.rootEntity)
      }
    },

    getTableTotalCount () {
      api.fetchData({
        query: new Line(this.query.line).techLine(),
        countTotal: true,
        returnEntries: false
      }).then((response) => {
        this.table.hasFurtherData = false
        this.table.serverItemsLength = Number(response.data.totalCount)
      })
    },

    /**
     * @vuese
     * Processing table row click: opens object view page or selects root entity
     * @arg table row item
     */
    onTableRowClick (item) {
      this.state.tableRowClick(item)
    },

    setQueryToUrl (line) {
      if (line !== this.queryParams) {
        this.$emit('push-query-params', line)
      }
    },

    /**
     * @vuese
     * Wraps all requests to api to set loading state while waiting a response
     * @arg function name
     */
    async asyncRequest (func) {
      this.loading = true
      let data
      try {
        data = await api[func](...Array.prototype.slice.call(arguments, 1))
        this.query.validation.status = true
      } catch (e) {
        this.query.validation.status = false
        this.query.validation.message = e.message
        data = {
          data: []
        }
      }
      this.loading = false
      return data
    },

    changeTableOptions () {
      this.state.reloadTableOnOptionChange(new Line(this.query.line).techLine())
    },

    getNamesAndTitles (items) {
      const names = filter(map(items, 'name'), null)
      const titles = filter(map(items, 'title'), null)
      const namesAndTitles = concat(names, titles)
      return [...new Set(namesAndTitles)]
    }
  }
}
</script>

<style lang="scss">
  .crudility-search-module {

    &__create-btn {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 10px;
    }

    .crudility-table-select {
      width: 250px;

      fieldset {
        border-bottom-right-radius: 0;
        border-top-right-radius: 0;
      }
    }
  }

  .v-input > * {
    margin-bottom: 0 !important;
    margin-top: 0 !important;
  }
</style>

